.sidebar{
    display:flex;
    flex-direction:column;
    flex: 0.35;
}

.sidebar_header{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-right: 1px solid lightgray;


}

.sidebar_header_left{
    align-items: center;


}

.sidebar_header_right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;

}

.sidear_search{
    display:flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;





}

.sidebar_search_container{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}


.sidebar_search_container>.MuiSvgIcon-root{
    color:green;
    padding: 10px
    

}
.sidebar_search_container> input{
    border: none;
    margin-left: 10px;
    width:100%;
    outline-width:0;

}

.sidebar_chat{
    flex: 1;
    background-color: white;
    overflow: scroll;

}

